import { useState, useEffect, useCallback } from 'react'
import { PerfilEscrita, MensagemChat, CorrecaoRedacao } from '@/lib/types/mentor'

interface UseMentorReturn {
  perfil: PerfilEscrita | null
  mensagens: MensagemChat[]
  isLoading: boolean
  isTyping: boolean
  corrigirRedacao: (texto: string) => Promise<CorrecaoRedacao | null>
  enviarMensagem: (conteudo: string, categoria?: string) => Promise<void>
  carregarHistorico: () => Promise<void>
  limparChat: () => void
}

export function useMentor(alunoId: string): UseMentorReturn {
  const [perfil, setPerfil] = useState<PerfilEscrita | null>(null)
  const [mensagens, setMensagens] = useState<MensagemChat[]>([])
  const [isLoading, setIsLoading] = useState(false)
  const [isTyping, setIsTyping] = useState(false)

  // Carregar perfil do aluno
  useEffect(() => {
    carregarPerfil()
  }, [alunoId])

  const carregarPerfil = async () => {
    try {
      // Simular carregamento do perfil do localStorage ou API
      const perfilSalvo = localStorage.getItem(`perfil_${alunoId}`)
      if (perfilSalvo) {
        setPerfil(JSON.parse(perfilSalvo))
      }
    } catch (error) {
      console.error('Erro ao carregar perfil:', error)
    }
  }

  const salvarPerfil = (novoPerfil: PerfilEscrita) => {
    try {
      localStorage.setItem(`perfil_${novoPerfil.aluno_id}`, JSON.stringify(novoPerfil))
      setPerfil(novoPerfil)
    } catch (error) {
      console.error('Erro ao salvar perfil:', error)
    }
  }

  const corrigirRedacao = useCallback(async (texto: string): Promise<CorrecaoRedacao | null> => {
    if (!texto.trim()) return null

    setIsLoading(true)
    setIsTyping(true)

    try {
      const response = await fetch('/api/mentor/redacao-personalizado', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          aluno_id: alunoId,
          texto: texto,
          perfil_atual: perfil
        })
      })

      if (!response.ok) {
        throw new Error('Erro na corre√ß√£o da reda√ß√£o')
      }

      const resultado = await response.json()
      
      // Atualizar perfil
      salvarPerfil(resultado.perfil_atualizado)

      // Criar objeto de corre√ß√£o
      const correcao: CorrecaoRedacao = {
        id: Date.now().toString(),
        aluno_id: alunoId,
        texto_original: texto,
        texto_corrigido: resultado.texto_corrigido,
        notas_competencias: resultado.notas_competencias,
        nota_total: resultado.nota_total,
        comentarios: resultado.comentarios,
        erros_detectados: resultado.erros_detectados,
        dicas_personalizadas: resultado.dicas_personalizadas,
        data_correcao: new Date().toISOString(),
        tempo_correcao: 0
      }

      // Salvar corre√ß√£o no hist√≥rico
      const historicoKey = `historico_${alunoId}`
      const historicoAtual = JSON.parse(localStorage.getItem(historicoKey) || '[]')
      historicoAtual.push(correcao)
      localStorage.setItem(historicoKey, JSON.stringify(historicoAtual))

      // Adicionar mensagens do mentor ao chat
      const mensagemCorrecao: MensagemChat = {
        id: Date.now().toString(),
        tipo: 'mentor',
        conteudo: formatarFeedbackCorrecao(resultado),
        timestamp: new Date(),
        categoria: 'correcao',
        correcao_id: correcao.id
      }

      setMensagens(prev => [...prev, mensagemCorrecao])

      return correcao

    } catch (error) {
      console.error('Erro ao corrigir reda√ß√£o:', error)
      
      const mensagemErro: MensagemChat = {
        id: Date.now().toString(),
        tipo: 'mentor',
        conteudo: 'Desculpe, ocorreu um erro ao corrigir sua reda√ß√£o. Tente novamente em alguns instantes.',
        timestamp: new Date(),
        categoria: 'geral'
      }
      
      setMensagens(prev => [...prev, mensagemErro])
      return null
    } finally {
      setIsLoading(false)
      setIsTyping(false)
    }
  }, [alunoId, perfil])

  const enviarMensagem = useCallback(async (conteudo: string, categoria = 'geral') => {
    if (!conteudo.trim()) return

    // Adicionar mensagem do usu√°rio
    const mensagemUsuario: MensagemChat = {
      id: Date.now().toString(),
      tipo: 'user',
      conteudo: conteudo,
      timestamp: new Date(),
      categoria: categoria as any
    }

    setMensagens(prev => [...prev, mensagemUsuario])
    setIsTyping(true)

    // Verificar se √© uma reda√ß√£o para corre√ß√£o
    if (conteudo.length > 100 && (conteudo.includes('reda√ß√£o') || conteudo.includes('tema') || categoria === 'redacao')) {
      await corrigirRedacao(conteudo)
      return
    }

    // Simular resposta do mentor
    setTimeout(() => {
      const respostaMentor = gerarRespostaMentor(conteudo, perfil)
      const mensagemMentor: MensagemChat = {
        id: (Date.now() + 1).toString(),
        tipo: 'mentor',
        conteudo: respostaMentor,
        timestamp: new Date(),
        categoria: categoria as any
      }

      setMensagens(prev => [...prev, mensagemMentor])
      setIsTyping(false)
    }, 1500)
  }, [perfil, corrigirRedacao])

  const carregarHistorico = useCallback(async () => {
    try {
      const historicoKey = `chat_${alunoId}`
      const chatSalvo = localStorage.getItem(historicoKey)
      if (chatSalvo) {
        const mensagensSalvas = JSON.parse(chatSalvo)
        setMensagens(mensagensSalvas.map((msg: any) => ({
          ...msg,
          timestamp: new Date(msg.timestamp)
        })))
      }
    } catch (error) {
      console.error('Erro ao carregar hist√≥rico:', error)
    }
  }, [alunoId])

  const limparChat = useCallback(() => {
    setMensagens([])
    localStorage.removeItem(`chat_${alunoId}`)
  }, [alunoId])

  // Salvar mensagens no localStorage
  useEffect(() => {
    if (mensagens.length > 0) {
      const chatKey = `chat_${alunoId}`
      localStorage.setItem(chatKey, JSON.stringify(mensagens))
    }
  }, [mensagens, alunoId])

  return {
    perfil,
    mensagens,
    isLoading,
    isTyping,
    corrigirRedacao,
    enviarMensagem,
    carregarHistorico,
    limparChat
  }
}

// Fun√ß√£o auxiliar para formatar feedback de corre√ß√£o
function formatarFeedbackCorrecao(resultado: any): string {
  const { notas_competencias, nota_total, comentarios, mensagem_motivacional } = resultado

  let feedback = `## üìù Corre√ß√£o Completa da sua Reda√ß√£o\n\n`
  
  feedback += `### üéØ **Nota Total: ${nota_total}/1000**\n\n`
  
  feedback += `### üìä **Notas por Compet√™ncia:**\n`
  feedback += `‚Ä¢ **C1** (Norma Culta): ${notas_competencias.C1}/200\n`
  feedback += `‚Ä¢ **C2** (Tema): ${notas_competencias.C2}/200\n`
  feedback += `‚Ä¢ **C3** (Organiza√ß√£o): ${notas_competencias.C3}/200\n`
  feedback += `‚Ä¢ **C4** (Coes√£o): ${notas_competencias.C4}/200\n`
  feedback += `‚Ä¢ **C5** (Proposta): ${notas_competencias.C5}/200\n\n`
  
  if (comentarios.length > 0) {
    feedback += `### üí° **Feedback Detalhado:**\n`
    comentarios.forEach((comentario: string) => {
      feedback += `${comentario}\n`
    })
    feedback += `\n`
  }
  
  feedback += `### üåü **Mensagem do Mentor:**\n${mensagem_motivacional}\n\n`
  
  feedback += `---\n*Corre√ß√£o realizada em ${new Date().toLocaleString('pt-BR')}*`
  
  return feedback
}

// Fun√ß√£o auxiliar para gerar respostas do mentor
function gerarRespostaMentor(mensagem: string, perfil: PerfilEscrita | null): string {
  const lowerMessage = mensagem.toLowerCase()
  
  // Respostas personalizadas baseadas no perfil
  if (perfil) {
    if (perfil.nivel_escrita === 'iniciante' && lowerMessage.includes('dif√≠cil')) {
      return `Entendo que pode parecer dif√≠cil no in√≠cio, ${perfil.nivel_escrita === 'iniciante' ? 'mas voc√™ est√° no caminho certo' : ''}! üí™\n\nCom base no seu perfil, vejo que voc√™ j√° tem ${perfil.historico_redacoes} reda√ß√£o(√µes) corrigida(s). Isso mostra dedica√ß√£o!\n\n**Dicas personalizadas para voc√™:**\n${perfil.recomendacoes_personalizadas.map(rec => `‚Ä¢ ${rec}`).join('\n')}\n\nLembre-se: cada reda√ß√£o √© uma oportunidade de crescer. Continue praticando! üåü`
    }
  }
  
  // Respostas padr√£o baseadas em palavras-chave
  if (lowerMessage.includes('nota') || lowerMessage.includes('pontua√ß√£o')) {
    return `üìä **Sobre as notas do ENEM:**\n\nCada compet√™ncia vale at√© 200 pontos:\n‚Ä¢ **C1**: Norma culta da l√≠ngua\n‚Ä¢ **C2**: Compreens√£o do tema\n‚Ä¢ **C3**: Organiza√ß√£o das informa√ß√µes\n‚Ä¢ **C4**: Coes√£o e coer√™ncia\n‚Ä¢ **C5**: Proposta de interven√ß√£o\n\n**Meta ideal:** 800+ pontos (m√©dia 160 por compet√™ncia)\n**Nota 1000:** Excel√™ncia em todas as compet√™ncias\n\nQuer que eu analise uma reda√ß√£o sua para ver como est√° seu desempenho? üéØ`
  }
  
  if (lowerMessage.includes('medo') || lowerMessage.includes('ansiedade') || lowerMessage.includes('nervoso')) {
    return `ü§ó **√â normal sentir ansiedade!**\n\nTodos os estudantes passam por isso. Aqui est√£o algumas estrat√©gias:\n\n**Para reduzir a ansiedade:**\n‚Ä¢ Pratique reda√ß√µes regularmente\n‚Ä¢ Simule condi√ß√µes de prova\n‚Ä¢ Respire fundo antes de come√ßar\n‚Ä¢ Lembre-se: voc√™ se preparou para isso!\n\n**T√©cnica de relaxamento:**\n1. Inspire por 4 segundos\n2. Segure por 4 segundos\n3. Expire por 4 segundos\n4. Repita 3 vezes\n\nVoc√™ √© capaz! Sua prepara√ß√£o far√° a diferen√ßa. üíô`
  }
  
  // Resposta padr√£o encorajadora
  return `Obrigado por compartilhar isso comigo! üòä\n\nEstou aqui para te ajudar em todos os aspectos do ENEM. Posso te auxiliar com:\n\nüìù **Reda√ß√£o**: Corre√ß√£o, estrutura, repert√≥rio\nüìö **Conte√∫dos**: Todas as mat√©rias\nüéØ **Estrat√©gias**: T√©cnicas de estudo e prova\nüí™ **Motiva√ß√£o**: Apoio emocional e metas\n\nO que voc√™ gostaria de trabalhar hoje? Pode me enviar uma reda√ß√£o, fazer uma pergunta espec√≠fica ou apenas conversar sobre seus estudos!\n\nEstamos juntos nessa jornada! üåü`
}